@use "global/var" as v;
@use "global/mixins" as m;

.Proyectos {
  display: flex;
  flex-direction: column;

  @include m.desktop {
    flex-direction: row;
    gap: 2rem;
  }
}

.Proyecto {
  margin-bottom: 5rem;
  img {
    width: 40rem;
    height: 20rem;
  }
  .Proyecto__info--links {
    .Link {
      a {
        color: v.$White;
      }
    }
  }
}
